/////////////////Carousel Modal/////////////////////////
.slider {
  position: relative;
  width: 80%;
  padding-bottom: 100px;
  height: 500px;
  margin: auto;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.slider-img {
  text-align: center;
  position: absolute;
  width: 100%;
  height: 500px;
  transition: all 0.5s;
  background: white;
}

.slider-img img {
  max-width: 900px;
  height: 500px;
}

.slider-img-prev {
  transform: translateX(-100%);
}

.slider-img-next {
  transform: translateX(100%);
}

.buttonSliderPrev {
  position: absolute;
  left: 0;
  color: white;
  cursor: pointer;
  z-index: 1;
  padding: 20px;
  font-size: 48px;
  border-radius: 4px;
  box-shadow: 9px 9px 6px 3px rgba(34, 60, 80, 0.1);
  transition: 0.5s linear;
}

.buttonSliderPrev:hover {
  transform: scale(1.2);
}

.buttonSliderNext {
  position: absolute;
  right: 0;
  color: white;
  cursor: pointer;
  transition: 0.5s linear;
  padding: 20px;
  font-size: 48px;
  border-radius: 4px;
  box-shadow: -9px 9px 6px 3px rgba(34, 60, 80, 0.1);
}

.buttonSliderNext:hover {
  transform: scale(1.2);
}

