.footer-wrapper--icon {
  list-style: none;
  display: flex;
  justify-content: center;

  li {
    margin: 10px;
  }
    a {
      text-decoration: none;
      background-color: #f1f1f1;
      color: #14B9D5;
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 30%;
      position: relative;
      overflow: hidden;

      i {
        font-size: 23px;
        transition: .3s ease;

      }
      &:before {
        position: absolute;
        content: '';
        width: 120%;
        height: 120%;
        background-color: #14B9D5;
        left: -110%;
        top: 90%;
        transform: rotate(45deg);
      }
      &:hover{
        i{
          transform: scale(1.3);
          color: #f1f1f1;
        }
        &:before{
          animation-name: hover;
          animation-duration: .6s;
          top: -10%;
          left: -10%;
        } }


    }



}
@keyframes hover {
  0%{
    top:-110%;
    left:90%;
  }
  50%{
    top:10%;
    left:-30%;
  }
  100%{
    top:-10%;
    left:-10%;
  }
}

